@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  overscroll-behavior: none;
  margin: 0;
  padding: 0;
  height: 100%;
}
/* home.css */

#home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 80px 20px 20px; /* Added top padding to account for navbar */
    box-sizing: border-box;
    background-color: #1a1a1a;
    color: #ffffff;
    overflow: hidden; /* Prevents extra scroll */
  }

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 800px;
}

.text-container {
  text-align: center;
}

.typewriter-text {
  margin-bottom: 2rem;
}

.typed-line {
  font-size: 2.5rem;
  font-family: 'Roboto Mono', monospace;
  margin-bottom: 0.5rem;
  min-height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.cursor {
  display: inline-block;
  width: 3px;
  margin-left: 5px;
  background-color: #ffffff;
  animation: blink 1s step-end infinite;
}

.phrase-container {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem 0;
}

.single-phrase {
  font-size: 2rem;
  color: #64ffda;
  font-family: 'Roboto Mono', monospace;
  margin: 0;
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
}

.fade-out {
  opacity: 0;
  transform: translateY(20px);
}

.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.resume-link-container {
  margin-top: 3rem;
}

.resume-link {
  background: none;
  border: 2px solid #64ffda;
  color: #64ffda;
  padding: 0.75rem 1.5rem;
  font-family: 'Roboto Mono', monospace;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.resume-link:hover {
  background-color: rgba(100, 255, 218, 0.1);
}

.resume-text {
  position: relative;
  z-index: 1;
}

@keyframes blink {
  from, to {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

/* Responsive styles */
@media (max-width: 768px) {
  .typed-line {
    font-size: 2rem;
  }
  
  .single-phrase {
    font-size: 1.5rem;
  }
  
  .resume-link {
    font-size: 1rem;
    padding: 0.6rem 1.2rem;
  }
}

@media (max-width: 480px) {
  .typed-line {
    font-size: 1.6rem;
  }
  
  .single-phrase {
    font-size: 1.2rem;
  }
  
  .resume-link {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }
}

/* Skills Grid Styles */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(100, 255, 218, 0.2);
}

.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  transition: all 0.3s ease;
  padding: 1rem;
  background-color: rgba(255, 255, 255, 0.25);
  border-radius: 8px;
}

.skill-item:hover {
  transform: translateY(-5px);
  background-color: rgba(255, 255, 255, 0.15);
}

.skill-icon {
  width: 50px;
  height: 50px;
  transition: transform 0.3s ease;
}

.skill-item:hover .skill-icon {
  transform: scale(1.1);
}

.skill-name {
  font-family: 'Roboto Mono', monospace;
  font-size: 0.9rem;
  color: #64ffda;
  font-weight: 500;
}

#projects {
    background-color: #1a1a1a;
    color: #ffffff;
    min-height: 100vh;
    padding: 100px 20px 40px;
    box-sizing: border-box;
  }
  
  .projects-container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .section-title {
    font-family: 'Roboto Mono', monospace;
    font-size: 2rem;
    color: #64ffda;
    margin-bottom: 2rem;
    text-align: center;
  }
  
  .projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    grid-gap: 2rem;
    gap: 2rem;
    padding: 1rem;
  }
  
  .project-card {
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid rgba(100, 255, 218, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(100, 255, 218, 0.1);
  }
  
  .project-image-container {
    position: relative;
    padding-top: 56.25%; /* 16:9 aspect ratio */
    overflow: hidden;
  }
  
  .project-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  
  .project-card:hover .project-image {
    transform: scale(1.05);
  }
  
  .project-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(26, 26, 26, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .project-card:hover .project-overlay {
    opacity: 1;
  }
  
  .view-project-btn {
    padding: 0.75rem 1.5rem;
    background-color: #64ffda;
    color: #1a1a1a;
    text-decoration: none;
    border-radius: 4px;
    font-family: 'Roboto Mono', monospace;
    font-weight: 500;
    transition: transform 0.2s ease;
  }
  
  .view-project-btn:hover {
    transform: scale(1.05);
  }
  
  .project-content {
    padding: 1.5rem;
  }
  
  .project-title {
    color: #64ffda;
    font-family: 'Roboto Mono', monospace;
    font-size: 1.25rem;
    margin: 0 0 1rem 0;
  }
  
  .project-description {
    color: #ffffff;
    line-height: 1.6;
    margin-bottom: 1rem;
  }
  
  .project-tags {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    margin-top: 1rem;
  }
  
  .project-tag {
    background-color: rgba(100, 255, 218, 0.1);
    color: #64ffda;
    padding: 0.25rem 0.75rem;
    border-radius: 15px;
    font-size: 0.85rem;
    font-family: 'Roboto Mono', monospace;
  }
  
  @media (max-width: 768px) {
    .projects-grid {
      grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
      grid-gap: 1.5rem;
      gap: 1.5rem;
    }
    
    .section-title {
      font-size: 1.75rem;
    }
  }
  
  @media (max-width: 480px) {
    .projects-grid {
      grid-template-columns: 1fr;
      grid-gap: 1rem;
      gap: 1rem;
    }
    
    .section-title {
      font-size: 1.5rem;
    }
    
    .project-content {
      padding: 1rem;
    }
    
    .project-title {
      font-size: 1.1rem;
    }
  }
#contact {
    background-color: #1a1a1a;
    color: #ffffff;
    min-height: 100vh;
    padding: 80px 10px 30px;
    box-sizing: border-box;
  }
  
  .contact-container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .section-title {
    font-family: 'Roboto Mono', monospace;
    font-size: 2rem;
    color: #64ffda;
    margin-bottom: 2rem;
    text-align: center;
  }
  
  .contact-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
    padding: 2rem;
    border: 1px solid rgba(100, 255, 218, 0.1);
  }
  
  .contact-left {
    display: flex;
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem; /* Reduced from 2rem */
  }
  
  .profile-image-container {
    width: 100%;
    max-height: 300px; /* Added max-height */
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid rgba(100, 255, 218, 0.2);
  }
  
  .profile-image {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Makes image cover the container */
    display: block;
    transition: transform 0.3s ease;
  }
  
  .profile-image:hover {
    transform: scale(1.02);
  }
  
  .contact-links {
    background-color: rgba(255, 255, 255, 0.05);
    padding: 1.25rem; /* Reduced from 1.5rem */
    border-radius: 8px;
    border: 1px solid rgba(100, 255, 218, 0.1);
  }
  
  .contact-links h3 {
    font-family: 'Roboto Mono', monospace;
    color: #64ffda;
    margin-bottom: 1rem; /* Reduced from 1.5rem */
    font-size: 1.2rem;
  }
  
  .contact-info {
    display: flex;
    flex-direction: column;
    grid-gap: 0.75rem;
    gap: 0.75rem; /* Reduced from 1rem */
  }
  
  .contact-item {
    display: flex;
    flex-direction: column;
    grid-gap: 0.25rem;
    gap: 0.25rem; /* Reduced from 0.5rem */
  }
  
  .contact-label {
    font-family: 'Roboto Mono', monospace;
    color: #64ffda;
    font-size: 0.9rem;
  }
  
  .contact-item a {
    color: #fff;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .contact-item a:hover {
    color: #64ffda;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    background-color: rgba(255, 255, 255, 0.05);
    padding: 1rem;
    border-radius: 8px;
    border: 1px solid rgba(100, 255, 218, 0.1);
    /* Remove or adjust the height property */
    height: auto;
}
  
  .form-group {
    display: flex;
    flex-direction: column;
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }
  
  .form-group label {
    font-family: 'Roboto Mono', monospace;
    color: #64ffda;
    font-size: 0.9rem;
  }
  
  .form-group input,
  .form-group textarea {
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(100, 255, 218, 0.2);
    border-radius: 4px;
    padding: 0.75rem;
    color: #fff;
    font-family: inherit;
    transition: border-color 0.3s ease;
  }
  
  .form-group textarea {
    min-height: 120px; /* Reduced from 150px */
    resize: vertical;
  }
  
  .form-group input:focus,
  .form-group textarea:focus {
    outline: none;
    border-color: #64ffda;
  }
  
  .submit-btn {
    background-color: transparent;
    color: #64ffda;
    border: 1px solid #64ffda;
    padding: 0.75rem 1.5rem;
    font-family: 'Roboto Mono', monospace;
    font-size: 1rem;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-top: 117px;
  }
  
  .submit-btn:hover {
    background-color: rgba(100, 255, 218, 0.1);
    transform: translateY(-2px);
  }
  
  @media (max-width: 768px) {
    .contact-content {
      grid-template-columns: 1fr;
    }
  
    .section-title {
      font-size: 1.75rem;
    }
  
    .profile-image-container {
      max-height: 250px; /* Reduced for mobile */
    }
  
    .contact-form {
      padding: 1.5rem;
    }
  }
  
  @media (max-width: 480px) {
    .section-title {
      font-size: 1.5rem;
    }
  
    .contact-links h3 {
      font-size: 1.1rem;
    }
  
    .profile-image-container {
      max-height: 200px; /* Further reduced for smaller screens */
    }
  
    .submit-btn {
      width: 100%;
    }
  }
/* navbar.css */

nav {
  background-color: rgba(26, 26, 26, 0.95); /* Matches home background with slight transparency */
  color: #ffffff;
  padding: 20px 40px;
  text-align: center;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  box-sizing: border-box;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(100, 255, 218, 0.1); /* Subtle cyan border */
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  grid-gap: 3rem;
  gap: 3rem;
}

nav li {
  margin: 0;
  position: relative;
}

nav a {
  color: #ffffff;
  text-decoration: none;
  font-family: 'Roboto Mono', monospace;
  font-size: 1.1rem;
  transition: color 0.3s ease;
  padding: 0.5rem 1rem;
  position: relative;
}

nav a:hover {
  color: #64ffda; /* Cyan accent color */
}

nav a.active {
  color: #64ffda;
}

nav a::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #64ffda;
  transition: width 0.3s ease;
}

nav a:hover::after,
nav a.active::after {
  width: 100%;
}

/* Responsive styles */
@media (max-width: 768px) {
  nav {
    padding: 15px 20px;
  }

  nav ul {
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }

  nav a {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  nav ul {
    grid-gap: 1rem;
    gap: 1rem;
  }

  nav a {
    font-size: 0.9rem;
    padding: 0.3rem 0.6rem;
  }
}
